*{
    box-sizing: border-box;
}

body {
    margin: 0;
    padding: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    background-color: #2f2f2f;
    height: 100%;
}

html,
body,
#root,
.container{
    height: 100%;
    display: flex;
    justify-content: center;
}

.loader{
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.movies{
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-wrap: wrap;
    padding: 50px;
    padding-top: 70px;
    width:90%;
}
.movies .movie{
    width:45%;
    background-color:none;
    margin-bottom: 70px;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    font-weight: 300;
    padding: 20px;
}

.movie img{
    position: relative;
    max-width: 150px;
    width:100px;
    margin-right: 30px;
}

.movie .movie__title,
.movie .movie__year{
    margin-left: -20px;
    font-weight: 300;

}

.movie h3{
    margin-bottom: 5px;
}

.movie .genres__genre{
    font-size: 12px;
    padding: 0;
    margin: 0;
    display: inline;
    margin: 5px;

}

.movie__genre li{
    margin-right: 10px;

}
